@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Titillium+Web:wght@300;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans&display=swap);
body {
  background: rgb(18, 18, 18);
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.container {
  padding-left: 10%;
  padding-right: 10%;
  margin-top: 100px;
  align-items: center;
  z-index: 0;
}

header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1;
}
nav {
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #24252a;
  padding: 30px 10px;
  height: 10px;
}
.logo {
  cursor: pointer;
  width: 65px;
  height: 60px;
  margin-left: 10px;
}
nav li,
nav a,
nav button {
  font-family: "Montserrat", sans-serif;
  font-weight: 500px;
  font-size: 16px;
  color: #ecf0f1;
  text-decoration: none;
  cursor: pointer;
}

.nav_links {
  list-style: none;
}
.nav_links li {
  display: inline-block;
  padding: 0 20px;
}

.nav_links a:hover,
.reponsive_nav_links a:hover {
  color: #0888a9;
  border-bottom: 1px solid white;
}
.reponsive_nav_links {
  display: none;
  position: absolute;
  flex-direction: column;
  top: 44px;
  right: 0;
  background-color: black;
  width: 100vw;
  height: auto;
  border: 1px solid black;
  line-height: 65px;
  transition: all 0.5s;
}
.reponsive_nav_links li {
  height: 10vh;
  text-align: center;
  vertical-align: middle;
  transform: rotate(30);
}
.btn {
  padding: 9px 25px;
  background-color: rgb(0, 130, 169, 1);
  border: none;
  border-radius: 50px;
  cursor: pointer;
  ransition: all 0.3 ease 0s;
  margin: 0 5px;
}
.btn:hover {
  background-color: rgb(0, 130, 169, 0.8);
}
.burger {
  display: none;
}
.reg_signin_zone {
  display: flex;
  flex-direction:row;
}
.reg_signin_zone > *{
  margin:0 5px;
}
.reg_signin_zone .Account {
  border:1px solid rgb(0, 130, 169, 1);
  border-radius: 10px;
  box-sizing: padding-box;
  padding:3px 5px;
  cursor: pointer;
}
. .Account:hover {
  background:rgb(0, 130, 169, 1);
  color:black;
}
.Account {
  color:white;
} 

@media (max-width: 960px) {
  body {
    overflow-x: hidden;
  }
  .burger {
    display: block;
    border: 1px solid white;
    padding: 6px;
    border-radius: 10px;
    cursor: pointer;
    margin-right: 10px;
    align-items: center;
  }
  .nav_links {
    display: none;
  }
  .reponsive_nav_links{
    display: none;
  }
  .reg_signin_zone{
    display: none;
  }
  .burger:hover {
    background: white;
  }
  .burger:hover i{
    color: black;
  }
}

.GameCard {
  width: 100%;
  cursor: pointer;
  color: white;
  text-decoration: none;
  box-sizing: border-box;
  padding:0 10px;
  margin:0 10px 25px 10px;
}
.GameCard:hover {
  opacity: 0.8;
}

.Image {
  width: 100%;
  height: 320px;
  border: 1px solid rbg(18, 18, 18);
  border-radius: 10px;
  background-origin: content-box;
}
.OfferCard {
  margin-top: 20px;
}
.Publisher {
  font-family: "Noto Sans JP", sans-serif;
  font-weight: bold;
}
.Name {
  color: #3380cb;
}
.Price {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  color: #dddddd;
}
.Price div {
  margin-right: 10px;
}
.Price .discount {
  border: 1px solid #dc143c;
  border-radius: 4px;
  background-color: #dc143c;
  padding: 2px 4px;
}
.Price .origin {
  text-decoration: line-through;
}
.Price .origin,
.Price .sale {
  line-height: 25px;
}

.row{
    display:grid;
    grid-template-columns:25% 25% 25% 25%;
}
.title{
    color:white;
    font-family: 'Titillium Web', sans-serif;
    font-size: 20px;
}
@media (max-width:1380px){
    .row{
        grid-template-columns:25% 25% 25% 25%;
    }
}
@media (max-width:1200px){
    .row{
        grid-template-columns:33,33% 33,33% 33,33%s;
    }
}

@media (max-width:950px){
    .row{
        grid-template-columns:50% 50%;
    }
}
@media (max-width:580px){
    .row{
        grid-template-columns:100%;
    }
}
#RegisterForm,
#LoginForm {
  display: flex;
  flex-direction: column;
  margin-top: 30px;
  width: 450px;
  justify-content: space-around;
  font-family: "Noto Sans KR", sans-serif;
  font-size: 14px;
}
#RegisterForm input,
#LoginForm input {
  margin: 5px 0;
  border: 1px solid rgb(18, 18, 18);
  border-radius: 8px;
  height: 32px;
}
#RegisterForm button,
#LoginForm button,.AdminPage a.actived {
  box-sizing: padding-box;
  -moz-box-sizing: padding-box;
  -webkit-box-sizing: padding-box;
  margin: 20px 60px 0 60px;
  height: 50px;
  cursor: pointer;
  font-family: "Noto Sans KR", sans-serif;
  position: relative;
  display: inline-block;
  line-height: 30px;
  color: #fff;
  text-decoration: none;
  font-size: 18px;
  letter-spacing: 2px;
  border-radius: 10px;
  background: rgb(18, 18, 18);
  text-align: center;
  transition: all 0.2s;
  border: 3px solid #0162c8;
}
#RegisterForm button:hover,
#LoginForm button:hover,.AdminPage a.actived:hover {
  cursor: pointer;
  background: #0162c8;
  border: none;
  color: black;
  font-weight: bold;
}
@media (max-width:450px){
  .OrderPage .row{
      width: 240px;
  }
}
#RegisterPage,
#LoginPage {
  align-items: center;
  display: flex;
  justify-content: space-around;
  color: #eeeeee;
  flex-direction: column;
}

.gameDetailPage {
    color:#CCCCCC;    
}

.gameDetailPage h2{
    font-family: 'Noto Sans', sans-serif;
    color:#DDDDDD;
}
.carousel div:first-child{
   height: 500px;
}
.carousel div iframe{
    width:100%;
    height:100%;
}
.carousel .thumbs-wrapper{
    height: 160px!important;
}
.carousel img{
    height: 100%;
    width: auto;
}

.gameDetailPage h2:last-child{
    font-size:18px;
    font-weight:normal;
}

.intro{
    display:grid;
    grid-template-columns: 70% 30%;
    margin-bottom: 30px;
}
.about{
    display:grid;
    grid-template-columns: 30% 70%;
    margin-bottom: 30px;
}
.infor{
    display:grid;
    grid-template-columns:33.34% 33.34% auto;
}
.infor > div{
    margin:5px 0;
}
#title{
    font-family: "Noto Sans JP", sans-serif;
    font-size:24px;
    font-weight: bold;
}
.buy_zones{
    display:flex;
    flex-direction:column;
    justify-content:space-around;
}
.buy_zones a,.buy_zones .download, .AdminPage a.active{
    box-sizing:padding;
    -moz-box-sizing: padding-box;
    -webkit-box-sizing: padding-box;
    margin:0 60px;
    font-family: "Noto Sans KR", sans-serif;
    position: relative;
    display: inline-block;
    line-height: 30px;
    color: #fff;
    text-decoration: none;
    font-size: 18px;
    letter-spacing: 2px;
    border-radius: 10px;
    background: rgb(18, 18, 18);
    text-align: center;
    transition: all 0.2s;
    border: 2px solid rgb(203, 57, 42);
    cursor: pointer;
    height: 45px;
    line-height: 42px;
    font-weight: bold;
}

.buy_zones a:hover,.buy_zones .download:hover,  .AdminPage a.active:hover{
    background: rgb(203, 57, 42);
}
.buy_zones button .dropdown{
    display:none;
    flex-direction: column;
    z-index: 1;
    background:black;
}
.buy_zones button:hover .dropdown{
    display:flex;
}
.dropdown a{
    text-decoration: none;
    margin-top:10px;

}
.specifications_zone{
    display:grid;
    grid-template-columns: 50% 50%;
    margin-top:20px;
}
.specifications_zone > div{
    display:flex;
    flex-direction:column;
    background:rgb(28, 28, 28);
    border:1px solid rgb(28, 28, 28);
    border-radius:10px;
    margin:0 10px;
    padding:5px 10px;
}
.specifications_zone > div b{
    margin-bottom:10px;
}
.specifications_zone > div div:nth-child(2n){
    color:#AAAAAA;
    font-weight: bold;
}
.specifications_zone > div div:nth-child(2n+1){
    color:#DDDDDD;
    margin-bottom: 15px;
}
@media (max-width:928px){
    .intro{
        grid-template-columns: 60% 40%;
    }
}
@media (max-width:620px){
    .intro{
        grid-template-columns:100%;
    }
    .intro >*{
        margin:5px 0;
    }
}
@media (max-width:880px){
    .about:last-child{
        grid-template-columns:100%;
    }
}
@media (max-width:630px){
    .specifications_zone{
        grid-template-columns:100%;
    }
    .specifications_zone > div{
        margin:10px 0;
    }
}



