}
.AdminPage .row > div:first-child hr{
    width: 100%;
}
.AdminPage .row > div:last-child{
    display:grid;
    grid-template-columns: 20% 20% 20% 20% 20%;
    box-sizing: border-box;
    margin:0 30px;
